
body {
  font-family: Helvetica, sans-serif;
  max-width: 1200px;
}

.react-autosuggest__container {
  position: relative;
  padding: 4px 0px;
}

.react-autosuggest__input {
  width: 500px;
  height: 30px;
  padding: 5px 20px;
  font-family: Helvetica, sans-serif;
  font-weight: 300;
  font-size: 16px;
  border: 1px solid #aaa;
  border-radius: 4px;
}

.react-autosuggest__input--focused {
  outline: none;
}

.react-autosuggest__input--open {
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}

.react-autosuggest__suggestions-container {
  display: none;
}

.react-autosuggest__suggestions-container--open {
  display: block;
  position: absolute;
  top: 51px;
  width: 540px;
  border: 1px solid #aaa;
  background-color: #fff;
  font-family: Helvetica, sans-serif;
  font-weight: 300;
  font-size: 16px;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
  max-height: 300px;
  overflow-y: auto;
  z-index: 2;
}

.react-autosuggest__suggestions-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.react-autosuggest__suggestion {
  cursor: pointer;
  padding: 10px 20px;
}

.react-autosuggest__suggestion--highlighted {
  background-color: #ddd;
}

.rangeslider-horizontal .rangeslider__handle {
    border-radius: 8px;
    width: 16px;
    height: 16px;
}
.rangeslider-horizontal .rangeslider__handle:after {
    width: 0px;
    height: 0px;
}
.rangeslider-horizontal .rangeslider__fill {
    background-color: #428ab38c;
}

.rangeslider-horizontal .weight {
    width: 140px;
}



a:link, a:visited, a:active {
    color: #25a;
    text-decoration: none;
}
a:hover {
    color: #48e;
    text-decoration: underline;
}

body {
    margin: 20px;
}

#root button {
    border: thin;
    margin: 2px 10px;
    /* padding: 5px 10px; */
    width: 30px;
    font-size: 125%;
    font-weight: bold;
    cursor: pointer;
}

button.plus  {
    color: #24a;
    background-color: #eef;
}
button.minus {
    color: #a24;
    background-color: #fee;
}

#root .weight li {
    display: inline-block;
    white-space: nowrap;
    width: 140px;
}

#root div.weight {
    display: inline-block;
    white-space: nowrap;
    width: 160px;
    margin: 1px 10px;
}

#root .weight input {
    padding: 5px 10px;
    margin: 2px 5px;
    width: 45px;
}

#root li {
    /* display: block; */
    list-style-type: none;
    width: 500px;
    margin: 2px;
}

#root .title a {
    display: inline-block;
    vertical-align: middle;
    width: 400px;
}

div.ui_section {
    display: inline-block;
    vertical-align: top;
    margin-right: 10px;
    min-width: 400px;
}


/* Tooltips shamelessly taken from example here:
 * https://www.w3schools.com/css/css_tooltip.asp
 */ 

/* Tooltip container */
.tooltip {
  position: relative;
  display: inline-block;
  /* border-bottom: 1px dotted black; */
}

/* Tooltip text */
.tooltip .tooltiptext {
  visibility: hidden;
  width: 300px;
  background-color: #aaa;
  color: #fff;
  /* text-align: center; */
  padding: 2px 10px;
  border-radius: 10px;
 
  /* Position the tooltip text - see examples below! */
  position: absolute;
  z-index: 1;
  top: -5em;
  left: 105%;
  font-size: 80%;
}

/* Show the tooltip text when you mouse over the tooltip container */
.tooltip:hover .tooltiptext {
  visibility: visible;
}


